import React from 'react';
import {
  BarChart,
  Bar,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
  ResponsiveContainer,
} from 'recharts';

// Mock data: one bar per key type, stacked by status
const data = [
  {
    keyType: 'Laundry',
    Available: 20,
    InUse: 8,
    Lost: 2,
  },
  {
    keyType: 'Trash',
    Available: 25,
    InUse: 4,
    Lost: 1,
  },
  {
    keyType: 'Master',
    Available: 1,
    InUse: 1,
    Lost: 1,
  },
];

export default function KeyStatusBarChart() {
  return (
    <ResponsiveContainer width="100%" height={220}>
      <BarChart data={data} margin={{ top: 16, right: 24, left: 0, bottom: 0 }}>
        <CartesianGrid strokeDasharray="3 3" />
        <XAxis dataKey="keyType" />
        <YAxis allowDecimals={false} />
        <Tooltip />
        <Legend />
        <Bar dataKey="Available" stackId="a" fill="#22c55e" name="Available" />
        <Bar dataKey="InUse" stackId="a" fill="#3b82f6" name="In Use" />
        <Bar dataKey="Lost" stackId="a" fill="#ef4444" name="Lost" />
      </BarChart>
    </ResponsiveContainer>
  );
}
